import React, { Component } from "react";
import ReactDOM from "react-dom";

import {
  List,
  NavBar,
  Icon,
  Switch,
  Toast,
  PullToRefresh,
  ListView
} from "antd-mobile";
import { setStore, getStore, removeStore } from "util/store";
import { StickyContainer, Sticky } from "react-sticky";

const Item = List.Item;
const Brief = Item.Brief;

import { setValue } from "@/redux/collection-word";
import { connect } from "react-redux";
import { get } from "util/fetch";
import "./index.less";

const getUrlVars = url => {
  var vars = {};
  var parts = url.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m, key, value) {
    vars[key] = value;
  });
  return vars;
};

const routePath = ["/api/english/collection", "/api/english/collection-word"];

const NUM_ROWS = 10;
let pageIndex = 0;

function genData(pIndex = 0) {
  const dataArr = [];
  for (let i = 0; i < NUM_ROWS; i++) {
    dataArr.push(`row - ${pIndex * NUM_ROWS + i}`);
  }
  return dataArr;
}
class index extends Component {
  state = {
    dataSource: new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2
    }),
    collection_id: "",
    collection: {},
    total: 0,
    collection_words: [],
    isTrain: true,
    height: document.documentElement.clientHeight,
    refreshing: true,
    isLoading: true,
    pageSize: 10,
    pageNo: 1
  };
  componentDidMount() {
    const hei = this.state.height - ReactDOM.findDOMNode(this.lv).offsetTop;

    this.setState({
      height: hei
    });
    this.rData = [];
    this.init();
  }
  init = () => {
    let query = this.props.location.search;
    let collection_id = getUrlVars(query)["collection_id"];
    let token = getUrlVars(query)["token"];
    if (token === undefined) {
      Toast.info("授权失效", 2);
      return;
    }
    setStore("token", token);
    this.state.collection_id = collection_id;
    get(`${routePath[0]}/${collection_id}`).then(res => {
      this.setState({
        collection: res.data[0]
      });
    });
    this.fetchWords();
  };
  fetchWords = () => {
    this.setState({
      isLoading: true
    });
    let params = {
      collection_id: this.state.collection_id,
      pageSize: this.state.pageSize,
      pageNo: this.state.pageNo
    };
    get(`${routePath[1]}`, params)
      .then(res => {
        this.rData = [...this.rData, ...genData(++pageIndex)];
        log(res);
        let collection_words = [...this.state.collection_words, ...res.data];
        this.rData = collection_words.map(item => {
          return item.word;
        });
        this.setState({
          dataSource: this.state.dataSource.cloneWithRows(this.rData),
          collection_words,
          total: res.count,
          pageNo: parseInt(res.pageNo) + 1,
          isLoading: false
        });
        this.props.setValue({ data: collection_words });
      })
      .catch(err => {
        Toast.info("认证失效", 2);
      });
  };
  handleClick = id => {
    const { isTrain } = this.state;
    if (isTrain) {
      this.props.history.push(`/train-word?collection_id=${id}`);
    } else {
      this.props.history.push(`/learn-word?collection_id=${id}`);
    }
  };
  gotoLast = () => {
    this.props.history.go(-1);
  };
  onEndReached = e => {
    log(e);
    let { total, pageNo, pageSize } = this.state;
    if (pageNo * pageSize - pageSize >= total) {
      return;
    }
    this.fetchWords();
  };
  onRefresh;
  onRefresh = () => {
    log("onrefresh");
  };
  render() {
    const navBar = (
      <NavBar
        mode="light"
        onLeftClick={this.gotoLast}
        leftContent={<Icon type="left" />}
        rightContent={[<Icon key="1" type="ellipsis" />]}
      >
        That will get it
      </NavBar>
    );
    const header = (
      <div className="header-container">
        <h2>词汇库</h2>
        <div className="header">
          <div className="title">
            {this.state.collection.name}
            <p>
              {this.state.collection.note} ( total:{this.state.total})
            </p>
          </div>
          <div className="switch">
            复习与否
            <Switch
              checked={this.state.isTrain}
              onChange={() => {
                this.setState({
                  isTrain: !this.state.isTrain
                });
              }}
            />
          </div>
        </div>
      </div>
    );
    const list = (
      <div className="list">
        {this.state.collection_words.map(item => (
          <div
            className="cell"
            key={JSON.stringify(item)}
            onClick={() => this.handleClick(item.id)}
          >
            <div>
              <p>
                {item.word}{" "}
                <span className="grey">[{item.phonetic_symbol}]</span>{" "}
              </p>
              <p className="grey">{item.definition}</p>
            </div>
          </div>
        ))}
      </div>
    );

    const row = (rowData, sectionID, rowID) => {
      log(rowID);
      return (
        <div
          className="cell"
          onClick={() =>
            this.handleClick(this.state.collection_words[parseInt(rowID)].id)
          }
        >
          <div>
            <p>
              {this.state.collection_words[parseInt(rowID)].word}{" "}
              <span className="grey">
                [{this.state.collection_words[parseInt(rowID)].phonetic_symbol}]
              </span>{" "}
            </p>
            <p className="grey">
              {this.state.collection_words[parseInt(rowID)].definition}
            </p>
          </div>
        </div>
      );
    };
    const row1 = rowData => <div>{rowData}</div>;
    const listview = (
      <ListView
        useBodyScroll
        className="list"
        ref={el => (this.lv = el)}
        dataSource={this.state.dataSource}
        style={{ height: this.state.height }}
        renderRow={row}
        pageSize={5}
        initialListSize={10}
        onEndReached={this.onEndReached}
        renderFooter={() => (
          <div style={{ lineHeight: "30px", textAlign: "center" }}>
            {this.state.isLoading ? "Loading..." : "到达底部"}
          </div>
        )}
      />
    );
    return (
      <div className="share-word-container">
        {navBar}
        {header}
        {listview}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  redux: state.collectionWord
});
const mapDispatchToProps = dispatch => {
  return {
    setValue: value => {
      dispatch(setValue(value));
    }
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(index);
